@import '../../../scss/variables';


.list-appointment {
    .active-sort {
        color: $accent1;
        margin-left: 3px;
    }

    .disable-sort {
        color: rgb(196, 196, 196);
        margin-left: 3px;
    }

    .w-95 {
        width: 95px;
    }

    .table {
        thead th {
            font-size: 14px;
        }

        button.btn-link {
            text-decoration: underline;
        }
    }
}

.floor-map-img {
    height: auto;
}

.space-name {
    cursor: pointer;
}

.pass-appointments {
    background-color: #f0f3f6;
}